'use client'

import Cookies from 'js-cookie'
import { Moon, Sun } from 'lucide-react'
import { usePathname, useRouter } from 'next/navigation'
import { useTheme } from 'next-themes'
import React from 'react'
import ReactCountryFlag from 'react-country-flag'
import { useTranslation } from 'react-i18next'

import {
  Menubar,
  MenubarContent,
  MenubarItem,
  MenubarMenu,
  MenubarSeparator,
  MenubarTrigger
} from '~/components/ui/menubar' // <-- shadcn menubar
import { Locale } from '~/lib/i18n/config'
import { cn } from '~/lib/utils'

const languages = [
  { code: 'en', name: 'English', countryCode: 'GB' },
  { code: 'ru', name: 'Русский', countryCode: 'RU' },
  { code: 'lv', name: 'Latviešu', countryCode: 'LV' },
  { code: 'nl', name: 'Nederlands', countryCode: 'NL' }
]

export function ThemeAndLanguageMenubar({ locale }: { locale: Locale }) {
  const router = useRouter()
  const pathname = usePathname()
  const { setTheme } = useTheme()
  const { t } = useTranslation('common')

  const changeLanguage = (newLang: string) => {
    Cookies.set('NEXT_LOCALE', newLang, { expires: 365 })
    const segments = pathname?.split('/') || []
    segments[1] = newLang
    router.push(segments.join('/') || `/${newLang}`)
  }

  const currentLang = languages.find((l) => l.code === locale)

  return (
    <Menubar className="flex items-center gap-4">
      {/* Theme menu */}
      <MenubarMenu>
        <MenubarTrigger className="rounded-xl px-2">
          <span className="sr-only">Theme</span>
          <Sun className="h-[1.2rem] w-[1.2rem] dark:hidden" />
          <Moon className="hidden h-[1.2rem] w-[1.2rem] dark:block" />
        </MenubarTrigger>
        <MenubarContent align="end">
          <MenubarItem onSelect={() => setTheme('light')}>{t('theme.light')}</MenubarItem>
          <MenubarItem onSelect={() => setTheme('dark')}>{t('theme.dark')}</MenubarItem>
          <MenubarSeparator />
          <MenubarItem onSelect={() => setTheme('system')}>{t('theme.system')}</MenubarItem>
        </MenubarContent>
      </MenubarMenu>

      {/* Language menu */}
      <MenubarMenu>
        <MenubarTrigger className="rounded-xl px-2">
          <span className="sr-only">Language</span>
          {currentLang && (
            <ReactCountryFlag
              svg
              countryCode={currentLang.countryCode}
              className="rounded-lg text-2xl"
            />
          )}
        </MenubarTrigger>
        <MenubarContent align="end">
          {languages.map(({ code, countryCode, name }) => (
            <MenubarItem
              key={countryCode}
              onSelect={() => changeLanguage(code)}
              className={cn('flex items-center gap-2', {
                'font-medium': code === locale
              })}
            >
              <ReactCountryFlag countryCode={countryCode} svg className="h-4 w-4" />
              <span className="text-sm">{name}</span>
            </MenubarItem>
          ))}
        </MenubarContent>
      </MenubarMenu>
    </Menubar>
  )
}
